.cardHolder:hover {
  -webkit-animation: cardHolderAnim 2s;
          animation: cardHolderAnim 2s;
}

/* Animation */
@-webkit-keyframes cardHolderAnim {
  from {
    -webkit-transform: scale3d(1, 1, 1);
            transform: scale3d(1, 1, 1);
  }

  25% {
    -webkit-transform: scale3d(1.02, 0.95, 1);
            transform: scale3d(1.02, 0.95, 1);
  }

  40% {
    -webkit-transform: scale3d(0.98, 1.05, 1);
            transform: scale3d(0.98, 1.05, 1);
  }

  50% {
    -webkit-transform: scale3d(1.05, 0.95, 1);
            transform: scale3d(1.05, 0.95, 1);
  }

  65% {
    -webkit-transform: scale3d(1.00, 1.00, 1);
            transform: scale3d(1.00, 1.00, 1);
  }

  75% {
    -webkit-transform: scale3d(1.02, 0.98, 1);
            transform: scale3d(1.02, 0.98, 1);
  }

  to {
    -webkit-transform: scale3d(1, 1, 1);
    transform: scale3d(1, 1, 1);
  }
}
@keyframes cardHolderAnim {
  from {
    -webkit-transform: scale3d(1, 1, 1);
            transform: scale3d(1, 1, 1);
  }

  25% {
    -webkit-transform: scale3d(1.02, 0.95, 1);
            transform: scale3d(1.02, 0.95, 1);
  }

  40% {
    -webkit-transform: scale3d(0.98, 1.05, 1);
            transform: scale3d(0.98, 1.05, 1);
  }

  50% {
    -webkit-transform: scale3d(1.05, 0.95, 1);
            transform: scale3d(1.05, 0.95, 1);
  }

  65% {
    -webkit-transform: scale3d(1.00, 1.00, 1);
            transform: scale3d(1.00, 1.00, 1);
  }

  75% {
    -webkit-transform: scale3d(1.02, 0.98, 1);
            transform: scale3d(1.02, 0.98, 1);
  }

  to {
    -webkit-transform: scale3d(1, 1, 1);
    transform: scale3d(1, 1, 1);
  }
}
